.boby{color:#000;display:flex;flex-direction:column;flex-wrap:wrap;align-items:center;justify-content:center;width:100%;height:100%}.boby h1{color:#523636;font:100px "Helvetica Neue",Helvetica,Arial,sans-serif;margin-bottom:14px}.boby div{position:relative}.boby div input{box-sizing:border-box;padding:16px 16px 16px 59px;width:550px;height:44px;box-shadow:0 2px 4px 0 rgba(0,0,0,.2),0 25px 50px 0 rgba(0,0,0,.1);border:none;font-size:16px;outline:none}.boby div label{cursor:pointer;left:14px;position:absolute;transform:rotate(90deg);height:44px}.boby section{position:relative;color:#000;box-shadow:0 2px 4px 0 rgba(0,0,0,.2),0 25px 50px 0 rgba(0,0,0,.1);width:550px;background:#fff}.boby section li{display:flex;flex-direction:row;flex-wrap:wrap;align-items:center;justify-content:space-between;height:44px;border-top:1px solid #e6e6e6;list-style:none;position:relative}.boby section li input{display:none}.boby section li input+label{margin-left:14px;width:20px;height:20px;cursor:pointer;border:1px solid #000;border-radius:50%}.boby section li input:checked+label::before{display:block;content:"âœ”";text-align:center;font-size:14px;border-radius:50%}.boby section li span{position:absolute;margin-left:59px;text-align:center}.boby section li button{margin-right:15px;border:none;background:none;font-size:29px;cursor:pointer}.boby aside{position:relative;color:#000;font-size:14px;border:1px solid #e6e6e6;display:flex;flex-direction:row;flex-wrap:wrap;align-items:center;background:#fff;justify-content:space-between;width:550px;height:29px;box-shadow:0 1px 1px rgba(0,0,0,.2),0 8px 0 -3px #f6f6f6,0 9px 1px -3px rgba(0,0,0,.2),0 16px 0 -6px #f6f6f6,0 17px 2px -6px rgba(0,0,0,.2)}.boby aside div{margin:0px 14px 0px 14px}.boby aside div span a{border:1px solid transparent;color:#000;padding:0px 4px 0px 4px;margin:0px 4px 0px 4px;text-decoration:none}.boby aside div span :hover{border:1px solid #e6e6e6;border-radius:2px}.boby aside div span :focus{border:1px solid #8a8a8a;border-radius:2px}.boby footer{display:flex;flex-direction:column;flex-wrap:wrap;align-items:center;font-size:12px;margin-top:49px;height:122px}.boby footer p{color:#665b5b;margin:5px}.boby footer p a{color:#665b5b;text-decoration:none}